import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import CustomCheckBox from "../../shared/utility";
import {FormControlLabel} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";

const Step10 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};

        switch (fieldName) {
            case 'isDisabled':
                updatedState.bioGraphicInfo[fieldName] = value;
                errors = {};
                if (value !== "No" && !updatedState.bioGraphicInfo.hasOwnProperty('disabilityInfo')) {
                    updatedState.bioGraphicInfo.disabilityInfo = {};
                } else {
                    delete updatedState.bioGraphicInfo.disabilityInfo;
                }
                setErrorMsg(fieldValidator(fieldName, updatedState.bioGraphicInfo, 'required', errors).error);
                break;
            case 'deaf':
            case 'blind':
            case 'other':
                if (updatedState.bioGraphicInfo.disabilityInfo.hasOwnProperty(fieldName)) {
                    delete updatedState.bioGraphicInfo.disabilityInfo[fieldName];
                } else {
                    updatedState.bioGraphicInfo.disabilityInfo = {
                        ...updatedState.bioGraphicInfo.disabilityInfo,
                        [fieldName]: {}
                    };
                }
                setErrorMsg({});
                break;
            case 'descDeaf':
                updatedState.bioGraphicInfo.disabilityInfo.deaf = {
                    [fieldName]: value
                };
                setErrorMsg(fieldValidator(fieldName, updatedState.bioGraphicInfo.disabilityInfo.deaf, 'required', errors).error);
                break;
            case 'descBlind':
                updatedState.bioGraphicInfo.disabilityInfo.blind = {
                    [fieldName]: value
                };
                setErrorMsg(fieldValidator(fieldName, updatedState.bioGraphicInfo.disabilityInfo.blind, 'required', errors).error);
                break;
            case 'descOther':
                updatedState.bioGraphicInfo.disabilityInfo.other = {
                    [fieldName]: value
                };
                setErrorMsg(fieldValidator(fieldName, updatedState.bioGraphicInfo.disabilityInfo.other, 'required', errors).error);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};

        let newForm = fieldValidator('isDisabled', state.bioGraphicInfo, 'required', errors);

        if (state.bioGraphicInfo.hasOwnProperty('disabilityInfo')) {

            if (Object.keys(state.bioGraphicInfo.disabilityInfo).length > 0) {
                if (state.bioGraphicInfo.disabilityInfo.hasOwnProperty('deaf')) {
                    newForm = fieldValidator('descDeaf', state.bioGraphicInfo.disabilityInfo.deaf, 'required', newForm.error);
                }
                if (state.bioGraphicInfo.disabilityInfo.hasOwnProperty('blind')) {
                    newForm = fieldValidator('descBlind', state.bioGraphicInfo.disabilityInfo.blind, 'required', newForm.error);
                }
                if (state.bioGraphicInfo.disabilityInfo.hasOwnProperty('other')) {
                    newForm = fieldValidator('descOther', state.bioGraphicInfo.disabilityInfo.other, 'required', newForm.error);
                }
            } else {
                newForm = {isValid: false, error: {msg: 'Select atleast one box'}};
            }
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Accommodation for Individuals with Disabilities and/or Impairments`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <h5 className="text-center mb-3">
                            Read the information in the Form I-90 instructions before completing this part.
                        </h5>

                        <MDBRow center className={`mb-2`}>
                            <h4>
                                Are you requesting an accommodation because of your disabilities and/or impairments?
                            </h4>

                            <FormControlLabel
                                className={`mb-0`}
                                control={
                                    <CustomCheckBox
                                        id={`disabledYes`}
                                        checked={state.bioGraphicInfo.isDisabled === "Yes"}
                                        onChange={e => onChange(e, "isDisabled", "Yes")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>Yes</span>)}
                            />
                            <FormControlLabel
                                className={`mb-0`}
                                control={
                                    <CustomCheckBox
                                        id={`disabledYes`}
                                        checked={state.bioGraphicInfo.isDisabled === "No"}
                                        onChange={e => onChange(e, "isDisabled", "No")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>No</span>)}
                            />
                        </MDBRow>

                        {state.bioGraphicInfo.hasOwnProperty('disabilityInfo') && <React.Fragment>
                            <MDBRow center className={`mb-3`}>
                                <MDBCol>
                                    <FormControlLabel
                                        className={`mb-0`}
                                        control={
                                            <CustomCheckBox
                                                id={`deaf`}
                                                checked={state.bioGraphicInfo.disabilityInfo.hasOwnProperty('deaf')}
                                                onChange={e => onChange(e, "deaf", "")}
                                                color="primary"
                                                inputProps={{'aria-label': 'secondary checkbox'}}
                                            />
                                        }
                                        label={(
                                            <span className={`labelClass`}>
                                                I am deaf or hard of hearing and request the following accommodation
                                                (If you are requesting a sign-language interpreter, indicate for which language
                                                (for example, American Sign Language)):
                                            </span>
                                        )}
                                    />
                                </MDBCol>

                                {state.bioGraphicInfo.disabilityInfo.hasOwnProperty('deaf') && <MDBCol>
                                    <TextField
                                        label="Description"
                                        error={!!errorMsg.descDeaf}
                                        helperText={errorMsg.descDeaf}
                                        type="text"
                                        multiline={true}
                                        id={`descDeaf`}
                                        autoComplete="off"
                                        rowsMax={5}
                                        rows={5}
                                        name="descDeaf"
                                        classes={{root: "custom-text-input"}}
                                        placeholder={`Information As per the example`}
                                        value={state.bioGraphicInfo.disabilityInfo.deaf.descDeaf || ''}
                                        onChange={e => onChange(e, 'descDeaf', e.target.value)}
                                        onBlur={e => onChange(e, 'descDeaf', e.target.value)}
                                        required
                                    />
                                </MDBCol>}

                            </MDBRow>

                            <MDBRow center className={`mb-3`}>
                                <MDBCol>
                                    <FormControlLabel
                                        className={`mb-0`}
                                        control={
                                            <CustomCheckBox
                                                id={`blind`}
                                                checked={state.bioGraphicInfo.disabilityInfo.hasOwnProperty('blind')}
                                                onChange={e => onChange(e, "blind", "")}
                                                color="primary"
                                                inputProps={{'aria-label': 'secondary checkbox'}}
                                            />
                                        }
                                        label={(
                                            <span className={`labelClass`}>
                                                I am blind or have a low vision and request the following accommodation:
                                            </span>
                                        )}
                                    />
                                </MDBCol>

                                {state.bioGraphicInfo.disabilityInfo.hasOwnProperty('blind') && <MDBCol>
                                    <TextField
                                        label="Description"
                                        error={!!errorMsg.descBlind}
                                        helperText={errorMsg.descBlind}
                                        type="text"
                                        multiline={true}
                                        id={`descBlind`}
                                        autoComplete="off"
                                        rowsMax={5}
                                        rows={5}
                                        name="descBlind"
                                        classes={{root: "custom-text-input"}}
                                        placeholder={`Information As per the example`}
                                        value={state.bioGraphicInfo.disabilityInfo.blind.descBlind || ''}
                                        onChange={e => onChange(e, 'descBlind', e.target.value)}
                                        onBlur={e => onChange(e, 'descBlind', e.target.value)}
                                        required
                                    />
                                </MDBCol>}

                            </MDBRow>

                            <MDBRow center className={`mb-3`}>
                                <MDBCol>
                                    <FormControlLabel
                                        className={`mb-0`}
                                        control={
                                            <CustomCheckBox
                                                id={`other`}
                                                checked={state.bioGraphicInfo.disabilityInfo.hasOwnProperty('other')}
                                                onChange={e => onChange(e, "other", "")}
                                                color="primary"
                                                inputProps={{'aria-label': 'secondary checkbox'}}
                                            />
                                        }
                                        label={(
                                            <span className={`labelClass`}>
                                                I have another type of disability and/or impairment(Describe the nature of your
                                                disability and/or impairment and the accommodation you are requesting)
                                            </span>
                                        )}
                                    />
                                </MDBCol>

                                {state.bioGraphicInfo.disabilityInfo.hasOwnProperty('other') && <MDBCol>
                                    <TextField
                                        label="Description"
                                        error={!!errorMsg.descOther}
                                        helperText={errorMsg.descOther}
                                        type="text"
                                        multiline={true}
                                        id={`descOther`}
                                        autoComplete="off"
                                        rowsMax={5}
                                        rows={5}
                                        name="descOther"
                                        classes={{root: "custom-text-input"}}
                                        placeholder={`Information As per the example`}
                                        value={state.bioGraphicInfo.disabilityInfo.other.descOther || ''}
                                        onChange={e => onChange(e, 'descOther', e.target.value)}
                                        onBlur={e => onChange(e, 'descOther', e.target.value)}
                                        required
                                    />
                                </MDBCol>}

                            </MDBRow>
                        </React.Fragment>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step10;
