import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBRow,} from "mdbreact";
import {FormControlLabel, TextField} from '@material-ui/core';
import Step from "./Step";
import {fieldValidator} from "./validator";
import CustomCheckBox from "../../shared/utility";

const Step1 = props => {
    const {state, nextStep} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'arToggle':
                updatedState.dontHaveARN = !updatedState.dontHaveARN;
                updatedState.arNumber = '';
                delete errors.arNumber;
                setErrorMsg(errors);
                break;
            case 'arNumber':
                updatedState.arNumber = value;
                setErrorMsg(fieldValidator('arNumber', updatedState, 'text', errorMsg).error);
                break;
            case 'uscisToggle':
                updatedState.dontHaveUSCIS = !updatedState.dontHaveUSCIS;
                updatedState.uscisOAN = '';
                delete errors.uscisOAN;
                setErrorMsg(errors);
                break;
            case 'uscisOAN':
                updatedState.uscisOAN = value;
                setErrorMsg(fieldValidator('uscisOAN', updatedState, 'text', errorMsg).error);
                break;
            case 'fname':
                updatedState.fname = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredChar', errorMsg).error);
                break;
            case 'lname':
                updatedState.lname = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredChar', errorMsg).error);
                break;
            case 'mName':
                updatedState.mName = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredChar', errorMsg).error);
                break;
            case 'prevNamesToggle':
                updatedState.isPrevNames = value;
                if (updatedState.isPrevNames) {
                    updatedState['prevNameDetails'] = {}
                } else {
                    delete updatedState.prevNameDetails;
                }
                delete errors.prevfname;
                delete errors.prevlname;
                delete errors.prevMName;
                setErrorMsg(fieldValidator('isPrevNames', updatedState, 'required', errors).error);
                break;
            case 'prevfname':
            case 'prevlname':
            case 'prevMName':
                updatedState.prevNameDetails[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState.prevNameDetails, 'requiredChar', errorMsg).error);
                break;
            default:
                break;

        }
        props.onChangeState(updatedState);
    };

    const onNext = () => {
        let errors = fieldValidator('arNumber', state, 'text').error;
        errors = fieldValidator('uscisOAN', state, 'text', errors).error;
        errors = fieldValidator('fname', state, 'requiredChar', errors).error;
        errors = fieldValidator('lname', state, 'requiredChar', errors).error;
        errors = fieldValidator('mName', state, 'requiredChar', errors).error;
        let newForm = fieldValidator('isPrevNames', state, 'required', errors);

        if (state.isPrevNames === "Yes") {
            errors = fieldValidator('prevfname', state.prevNameDetails, 'requiredChar', newForm.error).error;
            errors = fieldValidator('prevlname', state.prevNameDetails, 'requiredChar', errors).error;
            newForm = fieldValidator('prevMName', state.prevNameDetails, 'requiredChar', errors);
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Application to Replace Permanent Resident Card`}>
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`arnToggle`}
                                            checked={state.dontHaveARN}
                                            onChange={e => onChange(e, "arToggle", e.target.checked)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(
                                        <span className={`labelClass`}>I don't have Alien Registration Number</span>)}
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    error={!!errorMsg.arNumber}
                                    helperText={errorMsg.arNumber}
                                    label="Alien Registration Number"
                                    type="text"
                                    id={`arNumber`}
                                    name="arNumber"
                                    value={state.arNumber || ''}
                                    placeholder={`i.e. A-123456789`}
                                    classes={{root: "custom-text-input"}}
                                    disabled={state.dontHaveARN}
                                    onChange={e => onChange(e, 'arNumber', e.target.value)}
                                    onBlur={e => onChange(e, 'arNumber', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`uscisToggle`}
                                            checked={state.dontHaveUSCIS}
                                            onChange={e => onChange(e, "uscisToggle", e.target.checked)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>I don't have USCIS</span>)}
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    error={!!errorMsg.uscisOAN}
                                    helperText={errorMsg.uscisOAN}
                                    label="USCIS Online Account Number"
                                    type="number"
                                    classes={{root: "custom-text-input"}}
                                    id={`uscisOAN`}
                                    name="uscisOAN"
                                    placeholder={`i.e. 123456789123`}
                                    value={state.uscisOAN || ''}
                                    disabled={state.dontHaveUSCIS}
                                    onChange={e => {
                                        onChange(e, 'uscisOAN', e.target.value);
                                    }}
                                    onBlur={e => onChange(e, 'uscisOAN', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="First Name"
                                    error={!!errorMsg.fname}
                                    helperText={errorMsg.fname}
                                    type="text"
                                    id={`fname`}
                                    name="fname"
                                    placeholder={`First Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.fname || ''}
                                    onChange={e => onChange(e, 'fname', e.target.value)}
                                    onBlur={e => onChange(e, 'fname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Middle Name"
                                    error={!!errorMsg.mName}
                                    helperText={errorMsg.mName}
                                    type="text"
                                    id={`mName`}
                                    name="mName"
                                    placeholder={`Middle Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.mName || ''}
                                    onChange={e => onChange(e, 'mName', e.target.value)}
                                    onBlur={e => onChange(e, 'mName', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>
                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Family Name"
                                    error={!!errorMsg.lname}
                                    helperText={errorMsg.lname}
                                    type="text"
                                    id={`lname`}
                                    name="lname"
                                    placeholder={`Last Name or Family Name`}
                                    classes={{root: "custom-text-input"}}
                                    value={state.lname || ''}
                                    onChange={e => onChange(e, 'lname', e.target.value)}
                                    onBlur={e => onChange(e, 'lname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <h3 className={`text-center mb-2`}>
                                Has your name legally changed since the issuance of your Permanent Resident Card?
                            </h3>

                            <MDBRow center className={`mb-2`}>
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`prevNamesToggle`}
                                            checked={state.isPrevNames === "Yes"}
                                            onChange={e => onChange(e, "prevNamesToggle", "Yes")}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>Yes</span>)}
                                />
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`prevNamesToggleNo`}
                                            checked={state.isPrevNames === "No"}
                                            onChange={e => onChange(e, "prevNamesToggle", "No")}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(<span className={`labelClass`}>No</span>)}
                                />
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`prevNamesToggleNA`}
                                            checked={state.isPrevNames === "NA"}
                                            onChange={e => onChange(e, "prevNamesToggle", "NA")}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(
                                        <span className={`labelClass`}>N/A - I never recieved my previous card.</span>)}
                                />
                            </MDBRow>
                        </MDBRow>

                        {state.isPrevNames === "Yes" && <MDBRow center className={`mb-2`}>
                            <h3 className={`text-center mb-2`}>
                                Provide your name exactly as it is printed on your current Permanent Resident Card.
                            </h3>
                            <MDBCol md={`3`} lg={`4`}>
                                <TextField
                                    label="Previous First Name"
                                    error={!!errorMsg.prevfname}
                                    helperText={errorMsg.prevfname}
                                    type="text"
                                    id={`prevfname`}
                                    name="prevfname"
                                    placeholder={`Previous First Name`}
                                    value={state.prevNameDetails.prevfname || ''}
                                    onChange={e => onChange(e, 'prevfname', e.target.value)}
                                    onBlur={e => onChange(e, 'prevfname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol md={`3`} lg={`4`}>
                                <TextField
                                    label="Previous Last Name"
                                    error={!!errorMsg.prevlname}
                                    helperText={errorMsg.prevlname}
                                    type="text"
                                    id={`prevlname`}
                                    name="prevlname"
                                    placeholder={`Previous Last Name`}
                                    value={state.prevNameDetails.prevlname || ''}
                                    onChange={e => onChange(e, 'prevlname', e.target.value)}
                                    onBlur={e => onChange(e, 'prevlname', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol md={`3`} lg={`4`}>
                                <TextField
                                    label="Previous Middle Name"
                                    error={!!errorMsg.prevMName}
                                    helperText={errorMsg.prevMName}
                                    type="text"
                                    id={`prevMName`}
                                    name="prevMName"
                                    placeholder={`Previous Middle Name`}
                                    value={state.prevNameDetails.prevMName || ''}
                                    onChange={e => onChange(e, 'prevMName', e.target.value)}
                                    onBlur={e => onChange(e, 'prevMName', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">
                                    Please Fill out all the details
                                </div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step1;
