import React, {Component} from "react";
import {MuiPickersUtilsProvider} from '@material-ui/pickers';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {Redirect, Route, Switch} from "react-router-dom";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import Step6 from "./Step6";
import Step7 from "./Step7";
import Step8 from "./Step8";
import Step9 from "./Step9";
import Step10 from "./Step10";
import Step11 from "./Step11";
import Step12 from "./Step12";
import Step14 from "./Step13";
import Step15 from "./Step14";
import Step16 from "./Step15";
import Step17 from "./Step16";
import Step18 from "./Step17";
// import AllOptions from "./AllOptions";
import ThankYou from "./ThankYou";
import Decline from "./Decline";

export default class MultiStepForm extends Component {
    state = {
        bioGraphicInfo: {}
    };

    /*handleChangeField = (e = {}, name, value) => {
        this.setState(
            {
                [name]: value,
            },
            () => console.log(this.state)
        );
    };*/

    handleChangeState = newState => {
        this.setState(newState);
    }

    submitData() {
        console.log(this.state);
        /*axios
            .post("https://hooks.zapier.com/hooks/catch/1712527/o5y1jta/", state)
            .then((response) => console.log(response))
            .finally(() => console.log("Data Sent", {data: state}));*/
    }

    renderStep = (props) => {
        const {history} = props;
        const {path, params} = props.match;
        if (params.step === "thankyou") {
            this.submitData();
            return <ThankYou/>;
        } else if (params.step === "decline") {
            return <Decline/>;
        }
        const step = parseInt(params.step);
        const currentPath = `/${path.split("/")[1]}`;
        switch (step) {
            case 1:
                return (
                    <Step1
                        history={history}
                        nextStep={`${currentPath}/2`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 2:
                return (
                    <Step2
                        history={history}
                        prevStep={`${currentPath}/1`}
                        nextStep={`${currentPath}/3`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 3:
                return (
                    <Step3
                        history={history}
                        prevStep={`${currentPath}/2`}
                        nextStep={`${currentPath}/4`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 4:
                return (
                    <Step4
                        history={history}
                        prevStep={`${currentPath}/3`}
                        nextStep={`${currentPath}/5`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 5:
                return (
                    <Step5
                        history={history}
                        prevStep={`${currentPath}/4`}
                        nextStep={`${currentPath}/6`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 6:
                return (
                    <Step6
                        history={history}
                        prevStep={`${currentPath}/5`}
                        nextStep={`${currentPath}/7`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 7:
                return (
                    <Step7
                        history={history}
                        prevStep={`${currentPath}/6`}
                        nextStep={`${currentPath}/8`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 8:
                return (
                    <Step8
                        history={history}
                        prevStep={`${currentPath}/7`}
                        nextStep={`${currentPath}/9`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 9:
                return (
                    <Step9
                        history={history}
                        prevStep={`${currentPath}/8`}
                        nextStep={`${currentPath}/10`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 10:
                return (
                    <Step10
                        history={history}
                        prevStep={`${currentPath}/9`}
                        nextStep={`${currentPath}/11`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 11:
                return (
                    <Step11
                        history={history}
                        prevStep={`${currentPath}/10`}
                        nextStep={`${currentPath}/12`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 12:
                return (
                    <Step12
                        history={history}
                        prevStep={`${currentPath}/11`}
                        nextStep={`${currentPath}/13`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 13:
                return (
                    <Step14
                        history={history}
                        prevStep={`${currentPath}/12`}
                        nextStep={`${currentPath}/14`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 14:
                return (
                    <Step15
                        history={history}
                        prevStep={`${currentPath}/13`}
                        nextStep={`${currentPath}/15`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 15:
                return (
                    <Step16
                        history={history}
                        prevStep={`${currentPath}/14`}
                        nextStep={`${currentPath}/16`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 16:
                return (
                    <Step17
                        history={history}
                        prevStep={`${currentPath}/15`}
                        nextStep={`${currentPath}/17`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 17:
                return (
                    <Step18
                        history={history}
                        prevStep={`${currentPath}/16`}
                        nextStep={`${currentPath}/thankyou`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            default:
                return <div>Invalid Step</div>;
        }
    };

    render() {
        const {path} = this.props.match;
        return (
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <Switch>
                    <Route
                        path="/start"
                        exact
                        render={() => <Redirect to={`${path}/1`}/>}
                    />
                    <Route path={`${path}/:step`} render={(props) => <>
                        {this.renderStep(props)}
                    </>}/>
                </Switch>
            </MuiPickersUtilsProvider>
        );
    }
}
