import React, {useEffect, useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import CustomCheckBox from "../../shared/utility";
import {FormControlLabel} from "@material-ui/core";

const SECTIONA = [
    {label: 'My previous card has been lost, stolen or destroyed.', value: 'cardDestroyed'},
    {label: 'My previous card was issued but never received.', value: 'issuedNotReceived'},
    {label: 'My existing card has been mutilated.', value: 'cardMutilated'},
    {
        label: 'My existing card has incorrect data because of Department of Homeland Security (DHS) error. (Attach your existing card with incorrect data along with this application.)',
        value: 'incorrectData'
    },
    {
        label: 'My name or other biographic information has been legally changed since issuance of my existing card.',
        value: 'biographicInfoChanged'
    },
    {label: 'My existing card has already expired or will expire within six months.', value: 'existingCardExpire'},
    {
        label: 'I have reached my 14th birthday and am registering as required. My existing card will expire AFTER my 16th birthday.',
        value: 'cardExpireAfter16thBirthday'
    },
    {
        label: 'I have reached my 14th birthday and am registering as required. My existing card will expire BEFORE my 16th birthday.',
        value: 'cardExpireBefore16thBirthday'
    },
    {label: 'I am a permanent resident who is taking up commuter status.', value: 'commuterStatusPR'},
    {label: 'My Port-of-Entry (POE) into the United States will be: .', value: 'poeCity'},
    {label: 'I am a commuter who is taking up actual residence in United States.', value: 'commuterActualResidence'},
    {label: 'I have automatically converted to lawful permanent resident status', value: 'convertedLPR'},
    {
        label: 'I have a prior edition of the Alien Registration Card, or I am applying to replace my current Permanent Resident Card for a reason that is not specified above.',
        value: 'havePriorARC'
    }
];

const SECTIONB = [
    {label: 'My previous card has been lost, stolen, or destroyed.', value: 'cardDestroyed'},
    {label: 'My previous card was issued but not received.', value: 'issuedNotReceived'},
    {label: 'My existing card was mutilated.', value: 'cardMutilated'},
    {
        label: 'My existing card has incorrect data because of Department of Homeland Security (DHS) error. (Attach your existing card with incorrect data along with this application.)',
        value: 'incorrectData'
    },
];

const Step6 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});
    const [section, setSection] = useState(false);

    useEffect(() => {
        if (state.status)
            setSection(state.status !== "conditionalPR" ? SECTIONA : SECTIONB);
    }, []);


    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'status':
                updatedState[fieldName] = value;
                if (value !== "conditionalPR") {
                    setSection(SECTIONA);
                } else {
                    setSection(SECTIONB);
                }
                updatedState.type = '';
                setErrorMsg(fieldValidator(fieldName, updatedState, 'required', errors).error);
                break;
            case 'type':
                updatedState[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'required', errors).error);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        errors = fieldValidator('status', state, 'required', errors).error;
        let newForm = fieldValidator('type', state, 'required', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Application Type`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <h4 className="text-center mb-2">
                            What is your current status based on the below values?
                        </h4>

                        <MDBRow center className={`mb-2`}>
                            <FormControlLabel
                                className={`mb-0 mt-2`}
                                control={
                                    <CustomCheckBox
                                        id={`lawfullPR`}
                                        checked={state.status === "lawfullPR"}
                                        onChange={e => onChange(e, "status", "lawfullPR")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>Lawful Permanent Resident</span>)}
                            />
                            <FormControlLabel
                                className={`mb-0 mt-2`}
                                control={
                                    <CustomCheckBox
                                        id={`commutorStatusPR`}
                                        checked={state.status === "commutorStatusPR"}
                                        onChange={e => onChange(e, "status", "commutorStatusPR")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>Permanent Resident In Commuter Status</span>)}
                            />
                            <FormControlLabel
                                className={`mb-0 mt-2`}
                                control={
                                    <CustomCheckBox
                                        id={`conditionalPR`}
                                        checked={state.status === "conditionalPR"}
                                        onChange={e => onChange(e, "status", "conditionalPR")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(<span className={`labelClass`}>Conditional Permanent Resident</span>)}
                            />
                        </MDBRow>

                        {section && <React.Fragment>
                            <h4 className="text-center mb-2">
                                Reason for Application (Select One that Applies)
                            </h4>

                            <MDBRow center className={`mb-2`}>
                                <MDBCol>
                                    {section.map((item, index) => <div>
                                        <FormControlLabel
                                            className={`mb-0 mt-2`}
                                            control={
                                                <CustomCheckBox
                                                    id={`${item.value}-${index}`}
                                                    checked={state.type === item.value}
                                                    onChange={e => onChange(e, "type", item.value)}
                                                    color="primary"
                                                    inputProps={{'aria-label': 'secondary checkbox'}}
                                                />
                                            }
                                            label={(<span className={`labelClass`}>{item.label}</span>)}
                                        />
                                    </div>)}
                                </MDBCol>
                            </MDBRow>
                        </React.Fragment>}

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step6;
