import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import TextField from "@material-ui/core/TextField";
import {DatePicker} from "@material-ui/pickers";
import CustomCheckBox from "../../shared/utility";
import {FormControlLabel} from "@material-ui/core";

const Step5 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};
        switch (fieldName) {
            case 'fatherName':
            case 'motherName':
            case 'classAdmission':
                updatedState[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'requiredWithSpace', errors).error);
                break;
            case 'dateOfAdmission':
                updatedState[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'required', errors).error);
                break;
            case 'usssNumber':
                updatedState[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'text', errors).error);
                break;
            case 'usssToggle':
                updatedState.dontHaveUSSS = !updatedState.dontHaveUSSS;
                updatedState.usssNumber = null;
                delete errors.usssNumber
                setErrorMsg(errors);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};
        errors = fieldValidator('fatherName', state, 'requiredWithSpace', errors).error;
        errors = fieldValidator('motherName', state, 'requiredWithSpace', errors).error;
        errors = fieldValidator('classAdmission', state, 'requiredWithSpace', errors).error;
        let newForm = fieldValidator('dateOfAdmission', state, 'required', errors);

        if (!state.dontHaveUSSS) {
            newForm = fieldValidator('usssNumber', state, 'text', newForm.error);
        }

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Additional Information`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => history.push(prevStep)}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <h4 className="text-center mb-2">
                            Parental Information
                        </h4>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Mother's Name (First Name)"
                                    error={!!errorMsg.motherName}
                                    helperText={errorMsg.motherName}
                                    type="text"
                                    id={`motherName`}
                                    name="motherName"
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`Mother's First Name`}
                                    value={state.motherName || ''}
                                    onChange={e => onChange(e, 'motherName', e.target.value)}
                                    onBlur={e => onChange(e, 'motherName', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="Father's Name (First Name)"
                                    error={!!errorMsg.fatherName}
                                    helperText={errorMsg.fatherName}
                                    type="text"
                                    id={`fatherName`}
                                    name="fatherName"
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`Father's First Name`}
                                    value={state.fatherName || ''}
                                    onChange={e => onChange(e, 'fatherName', e.target.value)}
                                    onBlur={e => onChange(e, 'fatherName', e.target.value)}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <h4 className="text-center mb-2">
                            US Admission Details
                        </h4>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <TextField
                                    label="Class of Admission"
                                    error={!!errorMsg.classAdmission}
                                    helperText={errorMsg.classAdmission}
                                    type="text"
                                    id={`classAdmission`}
                                    name="classAdmission"
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`Admission Class`}
                                    value={state.classAdmission || ''}
                                    onChange={e => onChange(e, 'classAdmission', e.target.value)}
                                    onBlur={e => onChange(e, 'classAdmission', e.target.value)}
                                    required
                                />
                            </MDBCol>
                            <MDBCol>
                                <DatePicker
                                    id="dateOfAdmission"
                                    label="Date of Admission"
                                    variant="inline"
                                    name={`dateOfAdmission`}
                                    disableFuture={true}
                                    format="dd/MM/yyyy"
                                    error={!!errorMsg.dateOfAdmission}
                                    helperText={errorMsg.dateOfAdmission}
                                    value={state.dateOfAdmission || null}
                                    autoOk={true}
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`Date of Admission`}
                                    onChange={e => onChange(e, 'dateOfAdmission', e.toLocaleDateString())}
                                    required
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>
                            <MDBCol>
                                <FormControlLabel
                                    className={`mb-0 mt-2`}
                                    control={
                                        <CustomCheckBox
                                            id={`usssNumberToggle`}
                                            checked={state.dontHaveUSSS}
                                            onChange={e => onChange(e, "usssToggle", true)}
                                            color="primary"
                                            inputProps={{'aria-label': 'secondary checkbox'}}
                                        />
                                    }
                                    label={(
                                        <span className={`labelClass`}>Don't have U.S. Social Security Number</span>)}
                                />
                            </MDBCol>
                            <MDBCol>
                                <TextField
                                    label="U.S. Social Security Number"
                                    error={!!errorMsg.usssNumber}
                                    helperText={errorMsg.usssNumber}
                                    type="text"
                                    id={`usssNumber`}
                                    name="usssNumber"
                                    disabled={state.dontHaveUSSS}
                                    classes={{root: "custom-text-input"}}
                                    placeholder={`i.e. (123456789)`}
                                    value={state.usssNumber || ''}
                                    onChange={e => onChange(e, 'usssNumber', e.target.value)}
                                    onBlur={e => onChange(e, 'usssNumber', e.target.value)}
                                    required={!state.dontHaveUSSS}
                                />
                            </MDBCol>
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step5;
